
const ImpegnoSociale = () => {
    return(
        <>
            <h1>Impegno sociale</h1>
            <p>
                Dal 2006 la nostra azienda fa parte della rete WWOOF Italia, un movimento mondiale che mette in relazione volontari 
                e progetti rurali naturali promuovendo esperienze educative e culturali per contribuire a costruire una comunità globale sostenibile.
            </p>
            <p>
                Dal 2018 l'azienda Monte Spazzavento collabora con l'associazione di promozione sociale Gruppo Urtura Toscana APS supportando 
                le diverse attività socioculturali e interagendo con il volontariato civile. 
            </p>
            <p>
                Nel 2018 l'azienda Monte Spazzavento partecipa al Progetto ”Anch’io” per l’inserimento lavorativo di disabili e persone con problemi 
                di salute mentale nell’ambito dei bandi europei POR-FSE per il periodo 2014-2020. 
            </p>
            <p>
                A gennaio 2020 accoglie per sei mesi una migrante che ha partecipato al progetto PUOI (Protezione Unita a Obiettivo Integrazione), volto 
                a realizzare percorsi di inserimento socio-lavorativo per migranti regolari.
            </p>
        </>
    );
}

export default ImpegnoSociale;