import React, { useCallback, useEffect, useState } from 'react';
import FsLightbox from 'fslightbox-react';
import ReactPlayer from 'react-player';

import Gallery from 'react-photo-gallery';

import './Home.css';

function useWindowSize() {
    // Initialize state with undefined width/height so server and client renders match
    // Learn more here: https://joshwcomeau.com/react/the-perils-of-rehydration/
    const [windowSize, setWindowSize] = useState({
      width: undefined,
      height: undefined,
    });
    useEffect(() => {
      // Handler to call on window resize
      function handleResize() {
        // Set window width/height to state
        setWindowSize({
          width: window.innerWidth,
          height: window.innerHeight,
        });
      }
      // Add event listener
      window.addEventListener("resize", handleResize);
      // Call handler right away so state gets updated with initial window size
      handleResize();
      // Remove event listener on cleanup
      return () => window.removeEventListener("resize", handleResize);
    }, []); // Empty array ensures that effect is only run on mount
    return windowSize;
}

//import 'bootstrap/dist/css/bootstrap.min.css';
//import { Container, Row, Col } from 'react-bootstrap';

const Home = () => {

    const [toggler, setToggler] = useState(false);
    const [srcIdx, setSrcIdx] = useState(0);

    const [pW, setPW] = useState();
    const [pH, setPH] = useState();
    
    const size = useWindowSize();

    useEffect(() => {
        let ww = size.width;
        if (ww > 576)
            ww = size.width * 2 / 3;
        setPW(ww);
        setPH(ww * 9 / 16);
    }, [size]);

    //const [playerWidth, setPlayerWidth] = useState(window.innerWidth);
    //const [playerHeight, setPlayerHeight] = useState(playerWidth * 9 / 16);

    const openLightbox = useCallback((e, { photo, idx }) => {
        setSrcIdx(photo.index);
        setToggler(o => !o);
    }, []);


    const photos = [
        {
            src: 'https://i.imgur.com/fsyrScY.jpg',
            width: 5,
            height: 3,
            index: 0
        },
        {
            src: 'https://upload.wikimedia.org/wikipedia/commons/c/c9/Moon.jpg',
            width: 4,
            height: 3,
            index: 1
        }    
    ]

    const images = photos.map((el) => el.src);

    return (
        <>
            <p style={{display:'flex', justifyContent:'center', fontSize: '23px', marginLeft: '10%', marginRight: '10%', marginTop: '100px', marginBottom: '100px'}}>
                Siamo un’azienda agricola contadina biologica e una fattoria didattica, a conduzione familiare.
            </p>
            <div style={{display:'flex', justifyContent:'center'}}>
                <ReactPlayer url='https://www.youtube.com/watch?v=ysz5S6PUM-U' playing={false} height={pH} width={pW} />
            </div>
            <p style={{display:'flex', justifyContent:'center', fontSize: '23px', marginLeft: '10%', marginRight: '10%', marginTop: '100px', marginBottom: '100px'}}>
                Amiamo Madre Natura e vivere in armonia con Essa, ne assecondiamo i Bisogni e
                accettiamo i Doni che regala, rispettando le Tradizioni e le pratiche Verdi. Offriamo i Frutti di
                questa meravigliosa Magia a persone interessate alla Qualità, alla Genuinità ed alla
                Naturalezza, nonché aperte ad una connessione profonda con Madre Natura e i suoi Magici
                Frutti.
            </p>
            <div className='gallery'>
                <Gallery photos={photos} onClick={openLightbox} />
            </div>
            
            <FsLightbox
                sourceIndex={srcIdx}
                toggler={toggler}
                sources={images}
            />
        </>
    );
}

export default Home;