import { Link } from 'react-router-dom';
import './Navbar.css';

const Nav = (props) => {

    return (
        <Link to={props.uri} style={{ textDecoration: 'none' }}>
            <div id='nav-button'>
                <h3 style={{fontFamily:'Poppins', fontWeight: '900', color: 'black', fontSize:'15px'}}>{props.title.toUpperCase()}</h3>
            </div>
        </Link>
    );

}

const Navbar = () => {

    return (
        <div id='nbar' >
            <Nav title='Home' uri='/' />
            <Nav title='Magici frutti' uri='/prodotti' />
            <Nav title='Chi siamo' uri='/chi-siamo' />
            <Nav title='Fattoria didattica' uri='/fattoria-didattica'  />
            <Nav title='Impegno sociale' uri='/impegno-sociale' />
            <Nav title='Contatti' uri='/contatti' />
        </div>
    );

}

export default Navbar;