import React from 'react';

import Navbar from './Navbar';

import './Header.css';

//import 'bootstrap/dist/css/bootstrap.min.css';
//import { Container, Row, Col } from 'react-bootstrap';

const Header = () => {

    return (
        <>
        <div style={{width: '100vw', height: '250px'}}>
            <div style={{position: 'absolute', zIndex: -1, backgroundColor: 'black', width: '100vw', height: '250px'}}></div>
            <div id='logotitle'>
                <h1 className='nopm' style={{fontFamily: 'rancho', fontSize: '50px'}}>Monte Spazzavento</h1>
                <h3 className='nopm' style={{fontFamily: 'Poppins', fontSize: '16px'}}>Azienda agricola biologica</h3>
            </div>
        </div>

        <Navbar />
        </>
    );

}

export default Header;