import './App.css';

import { BrowserRouter as Router, Redirect, Route, Switch } from 'react-router-dom';

import Header from './components/Header';
import Home from './components/Home';
import Prodotti from './components/Prodotti';
import ChiSiamo from './components/ChiSiamo';
import FattoriaDidattica from './components/FattoriaDidattica';
import ImpegnoSociale from './components/ImpegnoSociale';
import Contatti from './components/Contatti';

function App() {

	return (
		<Router basename={process.env.PUBLIC_URL}>
			<Header />

			<Switch>
				<Route exact path='/' render={() => <Home />}/>
				<Route exact path='/prodotti' render={() => <Prodotti />}/>
				<Route exact path='/chi-siamo' render={() => <ChiSiamo />}/>
				<Route exact path='/fattoria-didattica' render={() => <FattoriaDidattica />}/>
				<Route exact path='/impegno-sociale' render={() => <ImpegnoSociale />}/>
				<Route exact path='/contatti' render={() => <Contatti />}/>
				<Route><Redirect to='/' /></Route>
			</Switch>

		</Router>
	);

}

export default App;
